.single-post {
  .component.component-hero-with-cta {
    @extend .component-cream;
    &:after {
      content:none;
    }  
    color:$darkest;
    padding:em(84) 0;
    h1 {
      @include typeHeaderLarge;
      color:$darkest;
      line-height:1.2em;
    }
    .hero__meta {
      color:$grayMedium;
      margin-top:1em;
    }
  }
  .post__content {
    padding-top:em(92);
    padding-bottom:em(134);
    background:$light;
    .container > .row > .col-12 {
      max-width:800px;
    }
    p {
      margin-bottom:em(33);
    }
    h2 {
      margin-bottom:1em;
    }
    h3 {
      @include typeHeaderReduced;
    }
    h4 {
      @include typeHeaderSmall;
    }
    h2, h3, h4 {
      margin-top:1em;
      + h2, + h3, + h4 {
        margin-top:0;
      }
    }
    img {
      box-shadow:-3px 3px 10px rgba($darkest,.1);
      border-radius:em(5);
      max-width:100%;
    }
    .wp-block-column {
      h2,h3,h4,h5 {
        &:first-child {
          margin-top:0;
        }
      }
    }
  }
  .component-latest-posts {
    @extend .component-cream;
    padding:em(92) 0 0;
    .latest_posts__headline,.latest_posts__description {
      text-align:left;
    }
  }
}